/* src/components/Sidebar.css (CORREGIDO) */

/* --- SIDEBAR BASE --- */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 280px; 
  height: 100%;
  background: linear-gradient(180deg, rgba(44, 62, 80, 0.95) 0%, rgba(26, 37, 47, 0.98) 100%);
  backdrop-filter: blur(20px);
  color: white;
  padding-top: 70px; 
  transform: translateX(-100%);
  transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1000;
  box-shadow: 4px 0 40px rgba(0, 0, 0, 0.6);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.sidebar.open { 
  transform: translateX(0); 
  animation: slideIn 0.4s ease; 
}

@keyframes slideIn { 
  from { transform: translateX(-100%); opacity: 0.8; } 
  to { transform: translateX(0); opacity: 1; } 
}

/* Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(8px);
  z-index: 999;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn { 
  from { opacity: 0; } 
  to { opacity: 1; } 
}

/* Contenido interno */
.sidebar-content {
  display: flex;
  flex-direction: column;
  padding: 25px;
  gap: 12px;
  overflow-y: auto;
  height: calc(100% - 70px);
}

/* Scrollbar personalizado */
.sidebar-content::-webkit-scrollbar { 
  width: 8px; 
}

.sidebar-content::-webkit-scrollbar-track { 
  background: rgba(255, 255, 255, 0.05); 
  border-radius: 10px; 
  margin: 10px 0; 
}

.sidebar-content::-webkit-scrollbar-thumb {
  background: #3498db;
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: padding-box;
}

.sidebar-content::-webkit-scrollbar-thumb:hover {
  background: #2980b9;
  background-clip: padding-box;
}

/* Títulos de sección */
.sidebar h3 {
  margin: 25px 0 20px 0;
  text-align: center;
  color: #3498db;
  padding-top: 15px;
  border-top: 2px solid rgba(52, 152, 219, 0.3);
  font-size: 1em;
  font-weight: 700;
  letter-spacing: 1.5px;
  text-transform: uppercase;
}

/* BOTONES CORREGIDOS */
.sidebar-btn {
  width: 100%;
  display: flex; /* NUEVO: Flex para alinear contenido */
  align-items: center; /* NUEVO: Centrar verticalmente */
  gap: 12px; /* NUEVO: Espacio entre icono y texto */
  text-align: left;
  background: rgba(255, 255, 255, 0.05);
  color: white;
  padding: 14px 16px; /* AJUSTADO: Menos padding horizontal */
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.95em; /* NUEVO: Tamaño de fuente ajustado */
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
  position: relative;
  overflow: hidden;
  white-space: nowrap; /* NUEVO: Evitar saltos de línea */
  text-overflow: ellipsis; /* NUEVO: Agregar "..." si es muy largo */
  line-height: 1.4; /* NUEVO: Mejor altura de línea */
}

.sidebar-btn::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background: #3498db;
  transform: scaleY(0);
  transition: transform 0.3s ease;
}

.sidebar-btn:hover::before { 
  transform: scaleY(1); 
}

.sidebar-btn:hover {
  background: rgba(52, 152, 219, 0.2);
  transform: translateX(5px);
  box-shadow: 0 4px 20px rgba(52, 152, 219, 0.3);
  border-color: rgba(52, 152, 219, 0.5);
}

.sidebar-btn:active { 
  transform: translateX(3px); 
}

/* Iconos en botones - NUEVO */
.sidebar-btn svg,
.sidebar-btn img {
  flex-shrink: 0; /* No permitir que los iconos se encojan */
  width: 20px;
  height: 20px;
}

/* --- ESTILOS DEL PERFIL --- */
.sidebar-profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 15px;
  margin-bottom: 20px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.profile-info {
  display: flex;
  align-items: center;
  gap: 15px;
  width: 100%;
  margin-bottom: 15px;
}

.profile-photo {
  width: 65px;
  height: 65px;
  border-radius: 50%; 
  object-fit: cover;
  border: 3px solid transparent;
  background: linear-gradient(white, white) padding-box,
              linear-gradient(135deg, #3498db 0%, #2980b9 100%) border-box;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
  transition: all 0.3s ease;
  flex-shrink: 0; /* NUEVO: No permitir que la foto se encoja */
}

.profile-photo:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.5);
}

.profile-name {
  font-size: 1.1em; /* AJUSTADO: Ligeramente más pequeño */
  font-weight: 700;
  color: white;
  text-align: left;
  flex-grow: 1;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  letter-spacing: 0.5px;
  overflow: hidden; /* NUEVO */
  text-overflow: ellipsis; /* NUEVO */
  white-space: nowrap; /* NUEVO */
}

.btn-config {
  margin-top: 10px;
  padding: 10px 18px;
  font-size: 0.9em;
  width: 100%;
  background: #f39c12;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(243, 156, 18, 0.3);
  letter-spacing: 0.5px;
}

.btn-config:hover {
  transform: translateY(-2px);
  background: #e67e22;
  box-shadow: 0 6px 20px rgba(243, 156, 18, 0.5);
}

.btn-config:active { 
  transform: translateY(0); 
}

/* Responsive */
@media (max-width: 768px) {
  .sidebar { 
    width: 260px; 
  }
  
  .sidebar-profile { 
    padding: 15px 10px; 
  }
  
  .profile-photo { 
    width: 55px; 
    height: 55px; 
  }
  
  .profile-name { 
    font-size: 1em; 
  }
  
  .sidebar-btn {
    font-size: 0.9em; /* Más pequeño en móvil */
    padding: 12px 14px;
  }
  
  .sidebar h3 {
    font-size: 0.95em;
  }
}

/* Pantallas muy pequeñas */
@media (max-width: 360px) {
  .sidebar {
    width: 240px;
  }
  
  .sidebar-btn {
    font-size: 0.85em;
    padding: 10px 12px;
    gap: 8px;
  }
}